.datepicker-btns {
	display: flex;
	justify-content: space-between;
	margin-top: 8px;
}

.datepicker-btn {
	display: inline-block;
	border-radius: 24px;
	border: 1px solid #739FF3;
	padding: 5px;
	box-sizing: border-box;
	width: calc(50% - 10px);
	max-width: calc(50% - 10px);
	cursor: pointer;

	font-family: 'Manrope';
	font-style: normal;
	font-weight: 700;
	font-size: 14px;
	line-height: 20px;
	color: #739FF3;
	transition: .3s;

	span {
		position: relative;
		z-index: 5;
	}

	&--reject {
		&:hover {
			background: #F4F3F0;
			border: 1px solid #F4F3F0;
			color: #739FF3;
		}

	}

	&--accept {
		color: #FFFFFF;
		background: #739FF3;
		overflow: hidden;
		position: relative;
		z-index: 1;

		&::before {
			content: '';
			display: block;
			width: 100%;
			position: absolute;
			z-index: 1;
			left: 0;
			top: 0;
			width: 100%;
			height: 100%;
			background: linear-gradient(0deg, rgba(10, 80, 186, 0.2), rgba(10, 80, 186, 0.2)), #739FF3;
			opacity: 0;
			transition: .3s;
		}

		&:hover {
			&::before {
				opacity: 1;
			}
		}
	}
}




.flatpickr-calendar {
	width: 252px;
	padding: 12px 24px 24px 24px;
	box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.1);
	border-radius: 0;

	.select2-container {
		height: 40px;

		.select2-selection__arrow {
			margin-right: 6px;
			margin-left: 6px;
		}

		.select2-selection--single {
			height: 40px;

			.select2-selection__rendered {
				font-weight: 800;
				font-size: 12px;
				line-height: 16px;
				color: #031B4B;
			}
		}

		.select2-dropdown .select2-results .select2-results__option {
			font-weight: 800;
			font-size: 12px;
			line-height: 16px;
			color: #031B4B;


		}
	}
}

.flatpickr-calendar:before,
.flatpickr-calendar:after {
	display: none !important;
}

.flatpickr-weekdays {
	margin-top: 15px;
	padding-right: 12px;
}

span.flatpickr-weekday {
	text-transform: lowercase;
	font-family: 'Manrope';
	font-style: normal;
	font-weight: 500;
	font-size: 12px;
	line-height: 16px;
	color: #031B4B;

	&:last-child {
		color: #739FF3;

	}
}


.flatpickr-time {
	display: none !important;
}

.flatpickr-prev-month,
.flatpickr-next-month {
	display: none;
}


.flatpickr-rContainer {
	width: 100%;
}

.flatpickr-current-month {
	width: 100%;
	left: 0;
}

.flatpickr-days {
	width: 100%;
}

.dayContainer {
	width: 100%;
	min-width: 100%;
	max-width: 100%;
	padding-right: 12px;
	margin-top: 10px;
	height: 192px;

}

.flatpickr-day {
	font-family: 'Manrope';
	font-style: normal;
	font-weight: 500;
	font-size: 12px;
	line-height: 16px;
	color: #031B4B;
	height: 24px;
	width: 24px;
	max-width: 24px;
	margin-left: 4px;
	display: flex;
	justify-content: center;
	align-items: center;
	text-align: center;
	transition: all .3s;


	&:nth-child(7n-6) {
		margin-left: 0;

	}

	&:nth-child(7n) {
		color: #739FF3;

	}


}



.flatpickr-day.selected,
.flatpickr-day.startRange,
.flatpickr-day.endRange,
.flatpickr-day.selected.inRange,
.flatpickr-day.startRange.inRange,
.flatpickr-day.endRange.inRange,
.flatpickr-day.selected:focus,
.flatpickr-day.startRange:focus,
.flatpickr-day.endRange:focus,
.flatpickr-day.selected:hover,
.flatpickr-day.startRange:hover,
.flatpickr-day.endRange:hover,
.flatpickr-day.selected.prevMonthDay,
.flatpickr-day.startRange.prevMonthDay,
.flatpickr-day.endRange.prevMonthDay,
.flatpickr-day.selected.nextMonthDay,
.flatpickr-day.startRange.nextMonthDay,
.flatpickr-day.endRange.nextMonthDay {
	background: #739FF3;
	color: #fff !important;
	-webkit-box-shadow: none;
	box-shadow: none;
	border-color: #fff;

	color: #031B4B;

	&:nth-child(7n) {
		color: #739FF3;
	}

	&::before {
		opacity: 1;
	}
}

.flatpickr-day.inRange,
.flatpickr-day.prevMonthDay.inRange,
.flatpickr-day.nextMonthDay.inRange,
.flatpickr-day.today.inRange,
.flatpickr-day.prevMonthDay.today.inRange,
.flatpickr-day.nextMonthDay.today.inRange,
.flatpickr-day:hover,
.flatpickr-day.prevMonthDay:hover,
.flatpickr-day.nextMonthDay:hover,
.flatpickr-day:focus,
.flatpickr-day.prevMonthDay:focus,
.flatpickr-day.nextMonthDay:focus {
	cursor: pointer;
	outline: 0;
	background: #739FF3;
	color: #fff !important;
	border-color: #fff;
}


.flatpickr-day.flatpickr-disabled,
.flatpickr-day.flatpickr-disabled:hover,
.flatpickr-day.prevMonthDay,
.flatpickr-day.nextMonthDay,
.flatpickr-day.notAllowed,
.flatpickr-day.notAllowed.prevMonthDay,
.flatpickr-day.notAllowed.nextMonthDay {
	opacity: 0;
	pointer-events: none;
}


.flatpickr-custom-selects {
	display: flex;
	justify-content: space-between;
	padding-right: 12px;


}

.flatpickr-custom-select {

	.select2-container .select2-dropdown {
		overflow: auto;
		max-height: 186px;

	}

	&--month {
		width: 104px;

	}

	&--year {
		width: 68px;

	}
}

.flatpickr-month {
	display: none;
}




.form-page {
	position: relative;
	display: flex;
	flex-flow: column nowrap;
	width: 100%;
	height: auto;
	background: #F4F3F0;
	padding-top: 60px;
	padding-bottom: 34px;

	@include max(sm) {
		padding-top: 40px;
	}

	.container {
		&:first-child {
			.form-page__layout--left {
				border-top: 1px solid rgba(0, 85, 182, 0.3);

				@include max(lg) {
					border-top: none;
				}

			}

			.form-page__layout--right {
				border-top: 1px solid rgba(0, 85, 182, 0.3);

				@include max(lg) {
					border-top: none;
				}
			}
		}

	}

	&__line {
		position: relative;
		height: 1px;
		width: 100%;
		margin-left: auto;
		margin-right: auto;
		max-width: 1440px;
		background: rgba(0, 85, 182, 0.3);

		@include max(lg) {
			display: none;
		}
	}

	&__title {
		font-family: 'Manrope';
		font-style: normal;
		font-weight: 800;
		font-size: $fz32;
		line-height: $lh40;
		color: #031B4B;
		margin-bottom: 60px;

		@include max(sm) {
			margin-bottom: 40px;
		}
	}

	&__layout {
		position: relative;
		width: 100%;
		display: flex;

		@include max(lg) {
			flex-flow: column nowrap;
		}

		&--left {
			position: relative;
			display: flex;
			flex: 1 0 368px;
			flex-flow: column nowrap;
			justify-content: flex-start;
			align-items: flex-start;
			margin-right: 32px;
			padding-top: 40px;
			padding-bottom: 66px;

			@include max(lg) {
				flex: 1 0 auto;
				margin-right: 0;
				padding-right: 0;
				padding-top: 0;
				padding-bottom: 32px;
				border-bottom: 1px solid rgba(0, 85, 182, 0.3);
			}
		}

		&--right {
			padding-top: 40px;
			padding-bottom: 66px;
			position: relative;
			display: flex;
			flex-flow: column nowrap;
			justify-content: flex-start;
			align-items: flex-start;
			flex: 1 1 auto;
			width: 100%;

			@include max(lg) {
				padding-top: 40px;
				border: none;
			}
		}
	}
}

.form {
	&__title {
		font-family: 'Manrope';
		font-style: normal;
		font-weight: 800;
		font-size: $fz24;
		line-height: $lh28;
		color: #031B4B;

		@include max(lg) {
			br {
				display: none;
			}
		}

		@include max(sm) {
			font-size: $fz20;
			line-height: $lh26;
		}
	}

	&__feedback {
		position: relative;
		width: 100%;
		display: flex;
		justify-content: flex-start;
		align-items: flex-start;
		flex-flow: column nowrap;

		&--program {
			.form__block {
				&:nth-last-child(-n+2) {
					@include max(sm) {
						margin-bottom: 0;
					}
				}
			}

			.selectDot--label {
				label {
					@include max(sm) {
						margin-top: 24px;
					}
				}
			}

		}

		&--entity {
			.form__block--customer {
				margin-top: 0;

			}

			.form__block--assent {
				margin-top: 36px;
			}
		}

		form {
			width: 100%;
		}
	}

	&__button {
		margin-top: 36px;

		@include max(xs) {
			width: 100%;
			max-width: 100%;
		}
	}

	&__block {
		position: relative;
		display: flex;
		flex-flow: column nowrap;
		width: 100%;
		max-width: 100%;
		margin-bottom: 24px;

		&--date {
			label {
				/*&::after {
					position: absolute;
					width: 20px;
					height: 20px;
					top: 37px;
					right: 22px;
				}*/
			}

			input {
				background: url(../img/svg/date.svg), #ffffff !important;
				background-size: 22px 22px !important;
				background-position: calc(100% - 21px) 13px !important;
				background-repeat: no-repeat !important;
			}
		}

		&__wrap {
			position: relative;
			display: flex;
			justify-content: flex-start;
			align-items: flex-start;
			flex-flow: column nowrap;
			width: 100%;

		}

		.selectDot--label {
			@include max(md) {
				flex-flow: row wrap;
			}
		}

		&__title {
			font-family: 'Manrope';
			font-style: normal;
			font-weight: 700;
			font-size: $fz20;
			line-height: $lh26;
			color: $blueDarck;
			margin-bottom: 24px;
		}

		&--customer {
			margin-top: 36px;
		}

		&--assent {
			margin-top: 36px;

			@include max(sm) {
				font-weight: 600;
				font-size: $fz14;
				line-height: $lh20;

			}
		}

		.search-checkbox--block {
			label:not(.checkbox-container) {
				flex-flow: row nowrap;
				align-items: flex-start;
				justify-content: flex-start;

				span {
					font-weight: 600;
					font-size: $fz16;
					line-height: $lh24;
					color: $blueDarck;
					text-align: left;

					@include max(sm) {
						font-weight: 600;
						font-size: $fz14;
						line-height: $lh20;

					}
				}
			}
		}

		&.not-selected {
			opacity: 0.5;
		}

		label {
			position: relative;
			width: 100%;
			display: flex;
			flex-flow: column nowrap;
			justify-content: flex-start;
			align-items: flex-start;

			&.error {
				input {
					border: 1px solid #E94141;
					background: url(../img/svg/form-error.svg), #ffffff;
					background-repeat: no-repeat;
					background-size: 24px 24px;
					background-position: calc(100% - 20px) 12px;

				}

				/*&::after {
					content: url(../img/svg/form-error.svg);
					width: 24px;
					height: 24px;
					position: absolute;
					right: 22px;
					bottom: 14px;
					display: block;
				}*/

				/*&::before {
					content: 'Информация об ошибке или незаполненном поле';
					width: auto;
					position: absolute;
					left: 0;
					bottom: -20px;
					display: inline-block;
					color: #E94141;
					font-style: normal;
					font-weight: 400;
					font-size: 13px;
					line-height: 16px;

					@include max(xs) {
						display: none;
					}
				}*/
			}

			b {
				position: relative;
				display: flex;
				font-family: 'Manrope';
				font-style: normal;
				align-items: center;
				font-weight: 400;
				font-size: 13px;
				line-height: 16px;
				color: #031B4B;
				margin-bottom: 8px;

				&.label {

					.label--tooltips {
						display: block;
						position: relative;
						margin-left: 9px;
						width: 14px;
						height: 14px;
					}


				}
			}
		}

		input {
			position: relative;
			width: 100%;
			display: flex;
			background: #fff;
			color: #031B4B;
			font-family: 'Manrope';
			font-style: normal;
			font-weight: 600;
			font-size: $fz16;
			line-height: $lh24;
			height: 48px;
			padding: 12px 16px;
			padding-top: 10px;
			cursor: pointer;

			&::placeholder {
				font-family: 'Manrope';
				font-style: normal;
				font-weight: 600;
				font-size: $fz16;
				line-height: $lh24;
				color: #C0C0C0;

			}

			&:focus {
				border: 1px solid #031B4B;
			}
		}

	}

	&__block-double {
		position: relative;
		display: flex;
		justify-content: space-between;
		align-items: flex-start;
		width: 100%;

		@include max(sm) {
			flex-flow: column nowrap;
		}

		.form__block {
			width: 100%;
			max-width: calc(50% - 16px);

			// label {
			// 	width: calc(100% - 16px);
			// }
			@include max(sm) {
				flex-flow: column nowrap;
				max-width: 100%;
			}
		}
	}

	.select2-container {
		border: none;
		background: #fff;
		color: #031B4B;
		font-family: 'Manrope';
		font-style: normal;
		font-weight: 600;
		font-size: $fz16;
		line-height: $lh24;
		height: 48px;
		padding: 12px 16px;
		padding-top: 10px;

		.select2-selection--single {
			display: flex;
			justify-content: space-between;
			align-items: center;

			.select2-selection__rendered {
				padding-left: 0;
				padding-right: 0;
				font-family: 'Manrope';
				font-style: normal;
				font-weight: 700;
				font-size: $fz17T;
				line-height: $lh24;
				color: #031B4B;
			}
		}

		.select2-dropdown {
			border: none;
			background: #FFFFFF;
			box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.1);

			.select2-results {
				padding-bottom: 16px;
				padding-top: 16px;
				border-top: 1px solid $blueDarck;

				.select2-results__option {
					font-family: 'Manrope';
					font-style: normal;
					font-weight: 400;
					font-size: $fz17T;
					line-height: $lh24;
					color: #031B4B;
					padding-left: 16px;
					transition: all .5s;

					&:hover {
						transition: all .5s;
						text-shadow: 0 0 0px #031B4B, 0 0 0px #031B4B;
					}
				}

				.select2-results__option--selected {
					font-weight: 700;
				}
			}
		}

		.select2-selection__arrow {
			position: relative;
			display: block;
			width: 20px;
			height: 10px;
			background: url(../img/svg/select2-selection__arrow.svg) no-repeat center center;
			background-size: contain;
			transition: transform .3s;
			transform: rotate(0deg);

			b {
				display: none;
			}

		}
	}

	.select2-container--open {
		transition: all .3s;

		.select2-selection__arrow {
			transition: transform .3s;
			transform: rotate(180deg);
		}
	}

	.periodSelect__date__value__month+.select2-container {
		margin-right: 32px;
		width: 144px;
	}

	.selectDot {
		display: flex;
		align-items: center;
		padding-right: 15px;
		margin-right: 15px;

		&:last-child {
			border-right: none;
		}

		&--title {
			font-style: normal;
			font-weight: 500;
			font-size: 14px;
			line-height: 20px;
			color: #2C2C2C;
		}

		&--label {
			position: relative;
			display: flex;
			justify-content: flex-start;


		}

		label {
			display: flex;
			align-items: center;
			flex-flow: row nowrap;
			width: auto;
			cursor: pointer;
			font-size: 14px;
			margin-right: 32px;

			input {
				display: none;
				max-width: 100%;
				border-radius: 3px;
				font-size: 14px;
				margin-top: 4px;
				margin-right: 11px;
			}

			span {
				font-family: 'Manrope';
				font-style: normal;
				font-weight: 600;
				font-size: 16px;
				line-height: 24px;
				color: #031B4B;
			}

			input:checked+b {
				background: $blueLight;
				border: 1px solid $blueLight;

				&::after {
					content: "";
					display: block;
					width: 8px;
					height: 8px;
					border-radius: 50%;
					background-color: #fff;
				}
			}

			input:checked+span {
				color: $blueLight;
			}

			b {
				display: flex;
				align-items: center;
				justify-content: center;
				width: 20px;
				height: 20px;
				margin-bottom: 0;
				margin-right: 10px;
				border-radius: 50%;
				border: 1px solid $blueDarck;
				box-sizing: border-box;

				&::after {
					display: none;
				}
			}
		}
	}
}

